<template>
  <div class="music">
    <div class="container">
      <div class="row">
        <div class="col-12">
          <div class="user-title mt-5">
            <h2>
              <i class="fas fa-compact-disc"></i>
              <router-link to="/music/all"> 所有音樂</router-link>
              <span v-if="userData"> / </span>
              <router-link to="/music/loveSong" v-if="userData">收藏音樂</router-link>
            </h2>
            <button type="button" class="btn btn-outline-primary btn-sm"
             data-toggle="modal" data-target="#filter-modal">分類篩選</button>
          </div>
        </div>
      </div>
      <router-view :userData="userData" v-if="userData"></router-view>
      <Area v-else/>
    </div>
  </div>
</template>

<script>
import firebase from 'firebase';
import $ from 'jquery';
import Area from './pages/MusicAllSong.vue'

export default {
  name: 'Music',
  data() {
    return {
      userData: null
    }
  },
  methods: {
    getUserData() {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/getData`;
      firebase.auth().onAuthStateChanged(function(user) {
        if (user) {
          vm.$http.post(url, {"url": `users/${user.uid}/basic/`}).then((response) => {
            vm.userData = response.data;
          })
        } else {
          vm.userData = null;
        }
      });
    },
  },
  components:{
    Area
  },
  created() {
    const vm = this;
    vm.getUserData();
  },
}
</script>

<style lang="scss" scoped>
.aplayer.aplayer-narrow{
  width: 100%;
  height: 150px;
  .aplayer-pic{
    width: 100%;
    height: 150px;
  }
}
.router-link-active{
  cursor: default;
  color: #333;
}

.music{
  background-image: url(../assets/img/bg/wave.svg);
  background-size: 80% auto;
  background-repeat: no-repeat;
  background-position: bottom left;
}
</style>
